/deep/ .el-carousel__indicators--horizontal .el-carousel__indicator--horizontal button {
  width: 8px;
  height: 8px;
  background: rgb(38,144,215);
  border-radius: 50%;
  opacity: 0.5;
}
/deep/ .el-carousel__indicators--horizontal .el-carousel__indicator--horizontal.is-active button {
  width: 24px;
  height: 8px;
  background: rgb(0,61,117);
  opacity: 0.5;
  border-radius: 10px;
}

.carousel-item_ {
  width: 100%;
  height: 100%;
  background: rgb(214,224,245);
  display: flex;
  justify-content: center;
}

.carousel-img_ {
  margin-left: 4px;
  margin-right: 4px;
  margin-top: 4px;
  margin-bottom: 25px;
  max-width: 99.2%;
  max-height: 100%;
}
